<template>
  <div class="category">
    <van-search
      shape="round"
      @focus="jumpSearch"
      placeholder="搜索商品信息"
    />
    <van-tabs scrollspy sticky>
      <van-tab
        class="products"
        v-for="cate in category"
        :key="cate.id"
        :title="cate.cate_name"
      >
        <div class="right">
          <h3>{{ cate.cate_name }}</h3>
          <ul class="childrens">
            <router-link :to="`/president/${child.id}?title=${child.cate_name}`"
             v-for="child in cate.children"
              :key="child.id" tag="li">
              <van-image :src="child.pic" />
              <span>{{ child.cate_name }}</span>
            </router-link>
          </ul>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import { mapMutations } from "vuex";
export default {
  name: "Category",
  data() {
    return {
      category: [],
    };
  },
  created() {
    this.changeLoading(true);
    this.$axios.get("api/category").then((d) => {
      this.category = d.data.data;
      this.changeLoading(false);
    });
  },
  methods: {
    jumpSearch() {
      this.$router.push("/search");
    },
    ...mapMutations(["changeLoading"]),
  },
};
</script>
<style lang="less">
.products{
  .right {
    h3 {
      font-size: 14px;
      text-align: center;
    }
    .childrens {
      padding: 5px 0;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      li {
        width: 33%;
        margin: 7px 0;
        img {
          display: block;
          margin: 0 auto;
          border-radius: 50%;
          width: 65%;
        }
        span {
          font-size: 12px;
          display: block;
          text-align: center;
        }
      }
    }
  }
}
.category{
  background-color: white;
  .van-tabs--line {
    .van-tabs__wrap {
      position: fixed;
      width: 90px;
      left: 0px;
      z-index: 1;
      height: auto;
      overflow: auto;
      padding: 0px 0px;
      .van-tabs__nav {
        display: flex;
        flex-wrap: wrap;
        padding: 0px 0px;
        .van-tab {
          width: 100%;
          height: 40px;
          line-height: 40px;
          span {
            &.van-tab__text {
              font-size: 13px;
              color: #666;
            }
          }
        }
        .van-tab--active {
          background-color: #fff;
          border-left: 0.06rem solid #fc4141;
          width: 100%;
          text-align: center;
          color: #fc4141;
          font-weight: 700;
          span {
            &.van-tab__text {
              color: #fc4141;
            }
          }
        }
        .van-tabs__line {
          display: none;
        }
      }
    }
  }
  .van-tabs__content {
    padding-left: 90px;
  }
}
</style>